<template>
  <div class="funcModule">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/funcModuleExample"></iframe>
        </div>
    </div>
    <div class="cy_center">
      <h2 class="cy_h2">funcModule 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
	import funcModule from "cy_static/funcModule"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;div id="test"&gt;内容显示区&lt;/div&gt;
&lt;script type="text/javascript"&gt;
    import funcModule from "cy_static/funcModule";
    funcModule("#test", {
        "keyId" : "H5_Mall_Entry",
        "colSpace" : 0,
        "paddingTop" : 5,
        "paddingBottom" : 13,
        "backgroundColor" : "#ffffff"
    });
&lt;/script&gt;
</xmp >

      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">构造方法</h2>				
            <p><a>funcModule</a>(dom : selector|String, params : Object = {}) : <a>Void</a></p>
            <p>功能球菜单，支持单列与多列可结束滑动插件使用</p>
            <table class="table-props">
            <tr>
                <th colspan="5">params 参数</th>
                </tr>
                <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
                </tr>
              <tr>
                    <td>keyId</td>
                    <td class="atn">String</td>
                    <td>""</td>
                    <td>广告推荐位</td>
                    <td class="text-c">Y</td>
                </tr>
                <tr>
                    <td>rowSpace</td>
                    <td class="atn">String</td>
                    <td>5</td>
                    <td>列表行高度</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>row</td>
                    <td class="atn">String</td>
                    <td>5</td>
                    <td>每行的列数</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>column</td>
                    <td class="atn">String</td>
                    <td>2</td>
                    <td>功能球行数</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>paddingTop</td>
                    <td class="atn">String</td>
                    <td>10</td>
                    <td>模块上边距</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>paddingBottom</td>
                    <td class="atn">String</td>
                    <td>10</td>
                    <td>模块下边距</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>paddingLeft</td>
                    <td class="atn">String</td>
                    <td>10</td>
                    <td>模块左边距</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>paddingRight</td>
                    <td class="atn">String</td>
                    <td>10</td>
                    <td>模块右边距</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>unLazyNum</td>
                    <td class="atn">String</td>
                    <td>0</td>
                    <td>不懒加载图片</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>backgroundColor</td>
                    <td class="atn">String</td>
                    <td>#ffffff</td>
                    <td>模块背景色</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>moduleSpm</td>
                    <td class="atn">String</td>
                    <td>""</td>
                    <td>模块SPM编码</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>moduleName</td>
                    <td class="atn">String</td>
                    <td>""</td>
                    <td>模块名称</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>data</td>
                    <td class="atn">Object||Array</td>
                    <td>""</td>
                    <td>本地数据[]</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>onBefore</td>
                    <td class="atn">Function</td>
                    <td>function(data, params, dom) {}</td>
                    <td>渲染前处理</td>
                    <td class="text-c">N</td>
                </tr>
                <tr>
                    <td>onAfter</td>
                    <td class="atn">Function</td>
                    <td>function(data, params, dom) {}</td>
                    <td>渲染后处理</td>
                    <td class="text-c">N</td>
                </tr>
            </table> 
      </div>




    </div> 
  </div>
</template>

<script>
import {trim} from "../../../assets/js/codeStyle";
export default {
  name: 'funcModule',
  data () {
    return {
      msg: 'funcModule',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      },
      cmOptions2:{
            readOnly:"true",
            theme : "mdn-like",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  }, 
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
  }, 
  methods:{
  }
}
</script>    
<style scoped>
  .cy_example .iframeBox{position:absolute;top:48px;width:100%;bottom:0}
</style>


